<template>
	<div id="dashboard">
		<h4>Employees</h4>

		<ul>
			<li v-for="employee in employees" :key="employee.id">{{ employee.dept +' : '+employee.employee_id}} : {{ employee.name}} &nbsp;&nbsp;&nbsp;
				<button>
					<router-link :to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">view</router-link>
				</button>
			</li>
		</ul>
		<div class="fixed-action-btn bottom">
			<router-link to="/new">
				<button>add</button>
			</router-link>
		</div>
	</div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data: () => ({
    employees: []
  }),
  created() {
    db.collection("employees")
      .orderBy("employee_id")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          //console.log(doc.data().employee_id);
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            name: doc.data().name,
            dept: doc.data().dept,
            position: doc.data().position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>

<style>
ul li {
  list-style: none;
  padding: 5px;
}
</style>
